/* pt-mono - latin_cyrillic */
@font-face {
  font-family: "PTMono";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("PTMono"), url("../fonts/pt-mono.woff2") format("woff2"), url("../fonts/pt-mono.woff") format("woff"); }

/* roboto - condensed - latin_cyrillic */
@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("Roboto Condensed"), url("../fonts/roboto-condensed.woff2") format("woff2"), url("../fonts/roboto-condensed.woff") format("woff"); }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden; }

a {
  text-decoration: none; }

a:link {
  color: #ffffff; }

*,
*::before,
*::after {
  box-sizing: border-box; }

img {
  width: 100%;
  height: auto; }

.hidden {
  display: none; }

body {
  font-family: "PTMono", "Arial", sans-serif;
  font-size: 16px;
  line-height: 24px;
  font-weight: 400;
  display: flex;
  flex-direction: column;
  min-height: 100vh; }

.include {
  display: none; }

.main {
  flex-grow: 1; }

.container {
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 10px; }

@media (max-width: 1023px) {
  .container {
    max-width: 1023px; } }

@media (max-width: 767px) {
  .container {
    max-width: 767px; } }

