    height: 38px; } }

@keyframes animation {
  0% {
    transform: rotate(0deg); }
  100% {
    transform: rotate(360deg); } }

.information__inner {
  display: flex;
  justify-content: space-around;
  max-width: 1440px;
  position: relative;
  padding-right: 0;
  padding-bottom: 97px; }

.information__main {
  max-width: 800px;
  display: flex;
  flex-direction: column;
  position: relative;
  padding-top: 55px; }

.information__link {
  color: #ff951be3;
  font-style: italic;
  animation: change 2.2s linear infinite; }

.information__header,
.information__text {
  margin-top: 0;
  color: #ffffff; }

.information__header {
  margin-bottom: 28px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 700;
  font-size: 80px;
  line-height: 94px;
  letter-spacing: 6px;
  text-transform: uppercase; }

.information__text {
  margin-bottom: 42px;
