  position: absolute;
  background-repeat: no-repeat;
  z-index: 0; }

.information__inner::before {
  content: "";
  right: 47px;
  top: -73px;
  width: 636px;
  height: 640px;
  background-image: url("../img/information-polygon-big.svg"); }

.information__inner::after {
  content: "";
  right: 319px;
