  width: 373px;
  height: 379px;
  background-image: url("../img/information-polygon-small.svg"); }

.information__button {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 700;
  color: #ffffff;
  font-size: 16px;
  line-height: 19px;
  padding: 17px 28px;
  background-color: #ff951be3;
  border: none;
