.information__scroll:hover,
.information__scroll:focus {
  color: #ff951be3; }

.information__button-text-mobile {
  display: none; }

@media (max-width: 1023px) {
  .information__inner {
    overflow: hidden;
    padding-top: 34px;
    padding-bottom: 164px; }
  .information__inner::before {
    right: -71px;
    top: 12px;
    width: 501px;
    height: 501px;
    background-image: url("../img/information-polygon-big-tablet.svg"); }
  .information__inner::after {
    content: "";
