    height: 300px;
    background-image: url("../img/information-polygon-small-tablet.svg"); }
  .information__image {
    width: 318px;
    height: 400px; }
  .information__main {
    max-width: 385px;
    padding-right: 0;
    padding-top: 24px; }
  .information__header {
    font-size: 60px;
    line-height: 70px;
    letter-spacing: 5px;
    margin-bottom: 31px; }
  .information__scroll {
    display: none; }
  .information__text {
    margin-bottom: 55px;
    padding-left: 0;
    font-size: 18px;
    line-height: 28px; }
  .information__text::before {
    display: none; }
  .information__button {
    padding: 17px 15px;
    margin-left: 0;
